@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
*{margin:0;padding:0;font-family:'Rubik', sans-serif}#root{background-color:#457b9d;display:flex;flex-flow:column;align-items:center;align-content:center;height:100vh}#root h1{padding:2rem 2rem 1rem 2rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:#a8dadc}#search{background-color:#1d3557;padding:1rem 1rem 1rem 4rem;width:100vw}#search input{margin-right:1rem;color:#1d3557;padding:0.1rem}#search input:hover,#search input:focus{transition:500ms;border:2px solid #393e46;outline:none}#search button{background-color:#f1faee;padding:0.1rem 0.5rem 0.2rem 0.5rem;border-radius:0.5rem;transition:300ms}#search button:hover{background-color:#457b9d;transition:300ms}main{background:#a8dadc;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;align-items:center;flex-flow:column;padding:1rem;border-radius:2.5rem}main section{border-bottom-left-radius:2.5rem;border-bottom-right-radius:2.5rem}main section p{margin:1rem}

